import { useState, useEffect } from "react";
import React from "react";
import { useNavigate, useLocation } from "react-router-dom";
import * as M from "../css/StyledNav";
import NeedLogin from "./NeedLogin";

const BACKEND_URL = "http://3.38.125.151";

const Nav = () => {
  const navigate = useNavigate();
  const location = useLocation(); //     
  const [token, setToken] = useState(localStorage.getItem("access_token")); // localStorage  
  const [userId, setUserId] = useState(null); // :  ID 
  const [showLoginModal, setShowLoginModal] = useState(false); //      

  const goToRemeberTree = async () => {
    if (token) {
      try {
        const response = await fetch(
          `${BACKEND_URL}/accounts/api/get-user-id-from-token`,
          {
            method: "GET",
            headers: {
              Authorization: `Bearer ${token}`, //   
            },
          }
        );

        if (response.ok) {
          const data = await response.json();
          const userId = data.userId; // API   ID 
          setUserId(userId); //  ID  
          console.log("User ID:", userId);

          const treeResponse = await fetch(
            `${BACKEND_URL}/api/rememberTree/user/${userId}/`,
            {
              method: "GET",
              headers: {
                Authorization: `Bearer ${token}`, //   
              },
            }
          );

          if (treeResponse.ok) {
            const treeData = await treeResponse.json();
            console.log("Remember Tree Data:", treeData);

            if (treeData.length > 0) {
              navigate("/rememberTree"); //  
            } else {
              alert(" .");
              navigate("/plantTreeStepOne"); //  
            }
          } else {
            navigate("/plantTreeStepOne"); //  
          }
        } else {
          setShowLoginModal(true); //       
        }
      } catch (error) {
        console.error("Error fetching user ID:", error);
        setShowLoginModal(true); //       
      }
    } else {
      setShowLoginModal(true); //      
    }
  };

  const goToMemorialHallList = () => {
    if (token) {
      navigate("/memorialHallList"); //  
    } else {
      setShowLoginModal(true); //      
    }
  };

  const goToHallSingup = () => {
    if (token) {
      navigate("/memorialHallSignup"); //  
    } else {
      setShowLoginModal(true); //      
    }
  };

  return (
    <M.Nav>
      {showLoginModal && <NeedLogin />}
      <M.Navbar>
        <M.NavItem isActive={location.pathname === "/"}>
          <a href="/">HOME</a>
          <hr />
        </M.NavItem>
        <M.NavItem isActive={location.pathname === "/memorialHallList"}>
          <a onClick={goToMemorialHallList}> </a>
          <hr />
        </M.NavItem>
        <M.NavItem isActive={location.pathname === "/rememberTree"}>
          <a onClick={goToRemeberTree}> </a>
          <hr />
        </M.NavItem>
        <M.NavItem isActive={location.pathname === "/memorialHallSignup"}>
          <a onClick={goToHallSingup}>  </a>
          <hr />
        </M.NavItem>
      </M.Navbar>
    </M.Nav>
  );
};

export default Nav;
