import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styles from "../css/StyledHelpModal.module.css";
import { useNavigate } from "react-router-dom";

const BACKEND_URL = "http://3.38.125.151";

const HelpModal = ({ onClose }) => {
  const navigate = useNavigate();

  const handleYesClick = () => {
    navigate("/deliveryProduct");
  };

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };

  return (
    <div className={`${styles.container} ${styles.fadeIn}`}>
      <div className={styles.innerContainer}>
        <div className={styles.closeBtn} style={{ zIndex: "1000" }}>
          <img
            src="./static/img/closeBtn.png"
            alt=" "
            onClick={onClose} // This will close the modal
          />
        </div>
        <Slider {...settings}>
          <div className={styles.pageOne}>
            <div className={styles.title}>  </div>
            <div className={styles.albumWp}>
              <img src="./static/img/albumGuide.png" alt="" />
              <div className={styles.a}>
                <div className={styles.subTitle}> </div>
                <div className={styles.detail}>
                      <br />
                      .
                </div>
              </div>
            </div>
            <div className={styles.postBoxWp}>
              <img src="./static/img/postBoxGuide.png" alt="" />
              <div className={styles.p}>
                <div className={styles.subTitle}> </div>
                <div className={styles.detail}>
                     <br />
                      .
                </div>
              </div>
            </div>
            <div className={styles.talkWp}>
              <img src="./static/img/talkGuide.png" alt="" />
              <div className={styles.t}>
                <div className={styles.subTitle}> </div>
                <div className={styles.detail}>
                       
                  <br />
                      .
                </div>
              </div>
            </div>
            <div className={styles.deliWp}>
              <img src="./static/img/deliGuide.png" alt="" />
              <div className={styles.d}>
                <div className={styles.subTitle}>   </div>
                <div className={styles.detail}>
                       
                  <br />      .
                  <br />       .
                </div>
              </div>
            </div>
          </div>
          <div className={styles.pageTwo}>
            <div className={styles.title}>  </div>
            <img
              src="./static/img/growBg.png"
              alt=""
              style={{
                width: "415px",
                height: "358px",
                position: "relative",
                left: "330px",
                bottom: "20px",
              }}
            />
            <img
              src="./static/img/growMotion.gif"
              alt=" "
              style={{
                width: "415px",
                height: "390px",
                position: "relative",
                left: "330px",
                bottom: "440px",
              }}
            />
            <div className={styles.growDetail}>
                 ( 3)  <br />{" "}
              <strong>-- -   </strong>
              .
              <br />      
              <br />    <strong>  </strong>
              . <br />
              <span className={styles.next}>
                     
              </span>
            </div>
          </div>
          <div className={styles.pageThr}>
            <div className={styles.modal}>
              <img
                className={styles.growImg}
                src="./static/img/growComplete.png"
                alt=" "
              />
              <div className={styles.completeMessage}>
                   !
              </div>
              <div className={styles.completeContent}>
                       ? <br />{" "}
                        
                . <br /> <strong>   ?</strong>
              </div>
              <div className={styles.no} onClick={onClose}>
                
              </div>
              <div className={styles.yes} onClick={handleYesClick}>
                
              </div>
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
};

const NextArrow = ({ className, style, onClick }) => {
  return (
    <img
      src="./static/img/nextBtn.png"
      alt=" "
      style={{
        width: "30px",
        height: "50px",
        position: "relative",
        left: "1040px",
        bottom: "745px",
        zIndex: "1000",
      }}
      className={className}
      onClick={onClick}
    />
  );
};

const PrevArrow = ({ className, style, onClick }) => {
  return (
    <img
      src="./static/img/prevBtn.png"
      alt=" "
      style={{
        width: "30px",
        height: "50px",
        position: "relative",
        top: "400px",
        left: "20px",
        zIndex: "1000",
      }}
      className={className}
      onClick={onClick}
    />
  );
};

export default HelpModal;
