import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import * as M from "../css/StyledMain";
import Nav from "./Nav";
import Info from "./Info";
import NeedLogin from "./NeedLogin";

const BACKEND_URL = "http://3.38.125.151";

const Main = () => {
  const navigate = useNavigate();
  const [showInfo, setShowInfo] = useState(false);
  const [token, setToken] = useState(localStorage.getItem("access_token")); // localStorage  
  const [userId, setUserId] = useState(null); // :  ID 

  useEffect(() => {
    // 5  Info   
    const timeoutId = setTimeout(() => {
      setShowInfo(true);
    }, 1);

    return () => {
      clearTimeout(timeoutId); //    
    };
  }, []);

  useEffect(() => {
    //   
    const validateToken = async () => {
      if (token) {
        try {
          const response = await fetch(`${BACKEND_URL}/token/verify/`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify({ token }),
          });

          if (response.status === 401) {
            //    
            localStorage.removeItem("access_token");
            setToken(null);
            setShowLoginModal(true);
          }
        } catch (error) {
          console.error("Token validation failed:", error);
          setShowLoginModal(true);
        }
      }
    };

    validateToken();
  }, [token]);

  const [showLoginModal, setShowLoginModal] = useState(false); //      

  const goToRemeberTree = async () => {
    if (token) {
      try {
        const response = await fetch(
          `${BACKEND_URL}/accounts/api/get-user-id-from-token`,
          {
            method: "GET",
            headers: {
              Authorization: `Bearer ${token}`, //   
            },
          }
        );

        if (response.ok) {
          const data = await response.json();
          const userId = data.userId; // API   ID 
          setUserId(userId); //  ID  
          //  ID     
          console.log("User ID:", userId);

          const treeResponse = await fetch(
            `${BACKEND_URL}/api/rememberTree/user/${userId}/`,
            {
              method: "GET",
              headers: {
                Authorization: `Bearer ${token}`, //   
              },
            }
          );

          if (treeResponse.ok) {
            const treeData = await treeResponse.json();
            console.log("Remember Tree Data:", treeData);
            //  ID     

            if (treeData.length > 0) {
              navigate("/rememberTree"); //  
            } else {
              alert(" .");
              navigate("/plantTreeStepOne"); //  
            }
          } else {
            navigate("/plantTreeStepOne"); //  
          }
        } else {
          setShowLoginModal(true); //       
        }
      } catch (error) {
        console.error("Error fetching user ID:", error);
        setShowLoginModal(true); //       
      }

      // navigate("/plantTreeStepOne");
    } else {
      setShowLoginModal(true); //      
    }
  };

  const goToMemorialHall = () => {
    if (token) {
      navigate("/memorialHallList");
    } else {
      setShowLoginModal(true); //      
    }
  };

  const goToHallSignup = () => {
    if (token) {
      navigate("/memorialHallSignup");
    } else {
      setShowLoginModal(true); //      
    }
  };

  return (
    <M.Body>
      <M.Container>
        {showLoginModal && <NeedLogin />}
        <Nav />
        <M.Content>
          <M.NavBtns>
            <M.NavBtnWrapper1>
              <M.NavBtnWrapper2>
                <a onClick={goToMemorialHall}> </a>
              </M.NavBtnWrapper2>
            </M.NavBtnWrapper1>
            <M.NavBtnWrapper1>
              <M.NavBtnWrapper2>
                <a onClick={goToRemeberTree}> </a>
              </M.NavBtnWrapper2>
            </M.NavBtnWrapper1>
            <M.NavBtnWrapper1>
              <M.NavBtnWrapper2>
                <a onClick={goToHallSignup}>
                   
                  <br />
                  
                </a>
              </M.NavBtnWrapper2>
            </M.NavBtnWrapper1>
          </M.NavBtns>
        </M.Content>
        <M.ImageGross>
          <img
            id="Gross"
            src={`${process.env.PUBLIC_URL}/static/img/Gross.png`}
            alt="Gross"
          />
        </M.ImageGross>
      </M.Container>
      {showInfo && <Info />}
    </M.Body>
  );
};

export default Main;
