import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import * as S from "../css/StyledSignup";
import axios from "axios";

const BACKEND_URL = "http://3.38.125.151";

const Signup1 = () => {
  const navigate = useNavigate();

  //   
  const [formData, setFormData] = useState({
    username: "",
    password: "",
    passwordConfirm: "",
    email: "",
  });

    //      
    const [errors, setErrors] = useState({
      username: "",
      password: "",
      passwordConfirm: "",
      email: "",
    });

      //      
    const [successMessages, setSuccessMessages] = useState({
      username: "",
      password: "",
      passwordConfirm: "",
      email: "",
    });

  //  ID  
  const [userId, setUserId] = useState(null);


  //       
  const handleInputChange = (e) => {
    const { name, value } = e.target;

    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));

    //      
    validateField(name, value);
  };

    //     
    const validateField = (fieldName, value) => {
      let error = "";
      let success = "";
  
      switch (fieldName) {
        case "username":
          const usernameRegex = /^[a-zA-Z0-9@./+/-/_]+$/;
          if (!value) {
            error = " .";
          } else if (!usernameRegex.test(value)) {
            error = " , , @, ., /, +, -, _    .";
          } else {
            success = "  .";
          }
          break;
        case "password":
          const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
          if (!value) {
            error = " .";
          } else if (!passwordRegex.test(value)) {
            error = "  8 , , ,   .";
          } else {
            success = "  .";
          }
          break;
        case "passwordConfirm":
          if (!value) {
            error = "  .";
          } else if (value !== formData.password) {
            error = "  .";
          } else {
            success = " .";
          }
          break;
        case "email":
          const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
          if (!value) {
            error = " .";
          } else if (!emailRegex.test(value)) {
            error = "   .";
          } else {
            success = "  .";
          }
          break;
        default:
          break;
      }
  
      setErrors((prevState) => ({
        ...prevState,
        [fieldName]: error,
      }));
  
      setSuccessMessages((prevState) => ({
        ...prevState,
        [fieldName]: success,
      }));
    };
  

  
  //   
  const handleComplete = () => {
    if (
      !formData.username ||
      !formData.password ||
      !formData.passwordConfirm ||
      !formData.email
    ) {
      alert("  .");
      return;
    }
    

    // API  
    const requestData = {
      username: formData.username,
      password: formData.password,
      password_confirm: formData.passwordConfirm,
      email: formData.email,
    };

    //   API 
    axios
      .post(`${BACKEND_URL}/accounts/register/step1/`, requestData)
      .then((response) => {
        console.log("Step 1 completed. Proceed to step 2.", response.data);
        const receivedUserId = response.data.userId;

        //     
        console.log("Received userId:", receivedUserId);
        setUserId(receivedUserId);

        // signup2      
        navigate("/signup2", { state: { userId: receivedUserId } });
      })
      .catch((error) => {
        console.error("Error while registering:", error);
        alert("   .");
      });
  };

  return (
    <S.Body>
      <S.Contaianer>
        <S.Title></S.Title>
        <S.Step1>
          <p>STEP 1</p>
        </S.Step1>

        <S.Step1Items>
          <S.Line>
            <img
              id="Logo"
              src={`${process.env.PUBLIC_URL}/static/img/line.png`}
              alt="Logo"
            />
          </S.Line>
          <S.Step1Item>
            <S.Number>
              <p>1</p>
            </S.Number>
            <S.NavName>
              <p></p>
            </S.NavName>
            <div>
              <input
                name="username"
                id="username"
                type="text"
                placeholder=", , @/./+/-/_  "
                value={formData.username}
                onChange={handleInputChange}
              />
              {errors.username && <S.ErrorMessage>{errors.username}</S.ErrorMessage>}
              {!errors.username && successMessages.username && (
                <S.SuccessMessage>{successMessages.username}</S.SuccessMessage>
              )}
            </div>
          </S.Step1Item>
          <S.Step1Item>
            <S.Number>
              <p>2</p>
            </S.Number>
            <S.NavName>
              <p></p>
            </S.NavName>
            <div>
              <input
                name="password"
                id="password"
                type="password"
                placeholder="(, ,   12 )"
                value={formData.password}
                onChange={handleInputChange}
              />
              {errors.password && <S.ErrorMessage>{errors.password}</S.ErrorMessage>}
              {!errors.password && successMessages.password && (
                <S.SuccessMessage>{successMessages.password}</S.SuccessMessage>
              )}
            </div>
          </S.Step1Item>
          <S.Step1Item>
            <S.Number>
              <p>3</p>
            </S.Number>
            <S.NavName>
              <p> </p>
            </S.NavName>
            <div>
              <input
                name="passwordConfirm"
                id="passwordConfirm"
                type="password"
                placeholder=" "
                value={formData.passwordConfirm}
                onChange={handleInputChange}
              />
              {errors.passwordConfirm && <S.ErrorMessage>{errors.passwordConfirm}</S.ErrorMessage>}
              {!errors.passwordConfirm && successMessages.passwordConfirm && (
                <S.SuccessMessage>{successMessages.passwordConfirm}</S.SuccessMessage>
              )}
            </div>
          </S.Step1Item>
          <S.Step1Item>
            <S.Number>
              <p>4</p>
            </S.Number>
            <S.NavName>
              <p></p>
            </S.NavName>
            <div>
              <input
                name="email"
                id="email"
                type="text"
                placeholder=""
                value={formData.email}
                onChange={handleInputChange}
              />
              {errors.email && <S.ErrorMessage>{errors.email}</S.ErrorMessage>}
              {!errors.email && successMessages.email && (
                <S.SuccessMessage>{successMessages.email}</S.SuccessMessage>
              )}
            </div>
          </S.Step1Item>
        </S.Step1Items>
        <S.NextBtn onClick={handleComplete}>
          <p></p>
        </S.NextBtn>
      </S.Contaianer>
    </S.Body>
  );
};

export default Signup1;
