import { useState, useEffect, useRef } from "react";
import styles from "../css/StyledTalkModal.module.css";

import { useNavigate, useLocation } from "react-router-dom";

const BACKEND_URL = "http://3.38.125.151";

function TalkModal({ onClose, myname }) {
  const [getAnswer, setAnswer] = useState("");
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [submittedAnswer, setSubmittedAnswer] = useState("");
  const [toastMessage, setToastMessage] = useState("");
  const [toastTimer, setToastTimer] = useState(null);
  const [isExpanded, setIsExpanded] = useState(false);
  const [hideAnswerWp, setHideAnswerWp] = useState(false);
  const [closeTimer, setCloseTimer] = useState(null);
  const location = useLocation();
  const [question, setQuestion] = useState("");
  // const question = "  ?   ?";
  const questRef = useRef(null);
  const answerWpRef = useRef(null);
  const chatBoxRef = useRef(null);
  const token = localStorage.getItem("access_token");
  const refreshToken = localStorage.getItem("refresh_token");
  const [questionId, setQuestionId] = useState(null);
  const navigate = useNavigate();
  console.log(":", isSubmitted);

  //   
  async function refreshAccessToken() {
    try {
      const response = await fetch(
        `${BACKEND_URL}/accounts/token/refresh/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ refresh: refreshToken }),
        }
      );

      if (response.ok) {
        const data = await response.json();
        localStorage.setItem("access_token", data.access);
        return data.access;
      } else {
        console.error("Failed to refresh access token");
        localStorage.removeItem("access_token");
        localStorage.removeItem("refresh_token");
        // Navigate to login page or handle the error accordingly
      }
    } catch (error) {
      console.error("Error refreshing access token:", error);
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        let response = await fetch(
          `${BACKEND_URL}/api/rememberTree/daily-question/`,
          {
            method: "GET",
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        //    
        if (response.status === 401) {
          const newAccessToken = await refreshAccessToken();
          if (newAccessToken) {
            response = await fetch(
              `${BACKEND_URL}/api/rememberTree/daily-question/`,
              {
                method: "GET",
                headers: {
                  Authorization: `Bearer ${newAccessToken}`,
                },
              }
            );
          } else {
            return; // Token refresh failed
          }
        }

        //         
        if (response.status === 404) {
          response = await fetch(
            `${BACKEND_URL}/api/daily-question/today-answers/`,
            {
              method: "GET",
              headers: {
                Authorization: `Bearer ${token}`,
              },
            }
          );
          //      
          if (response.ok) {
            const jsonData = await response.json();
            if (jsonData.length > 0) {
              const answerData = jsonData[0];
              console.log("3", answerData.answerText);
              console.log("4", answerData.question.questionText);
              setQuestion(answerData.question.questionText);
              setSubmittedAnswer(answerData.answerText);
              setIsSubmitted(true);
            }
          }
        } else if (response.status === 200) {
          setIsSubmitted(false);
          const jsonData = await response.json();
          console.log("Response data222:", jsonData);

          if (Array.isArray(jsonData)) {
            if (jsonData.length > 0) {
              const answerData = jsonData[0];
              console.log("3", answerData.answerText);
              console.log("4", answerData.question.questionText);
              setQuestion(answerData.question.questionText);
              // setSubmittedAnswer(answerData.answerText);
            }
          } else {
            // Handle object response
            console.log("", jsonData.questionText);
            setQuestion(jsonData.questionText);
            setQuestionId(jsonData.id);
          }
        }
      } catch (error) {
        console.error("An error occurred", error);
      }
    };

    fetchData();
  }, [token]);

  // useEffect(() => {
  //   const lastSubmissionDate = sessionStorage.getItem("lastSubmissionDate");
  //   const today = new Date().toISOString().split("T")[0];

  //   if (lastSubmissionDate === today) {
  //     setIsSubmitted(true);
  //     setHideAnswerWp(true);

  //     // const storedAnswer = sessionStorage.getItem("submitAns");
  //     // if (storedAnswer) {
  //     //   setSubmittedAnswer(storedAnswer);
  //     // }

  //     handleShowToast("5   .", 5000);
  //     const timer = setTimeout(() => {
  //       onClose();
  //     }, 5000);
  //     setCloseTimer(timer);
  //   }
  // }, [onClose]);

  async function submitAnswer() {
    if (!isSubmitted && getAnswer.trim() !== "") {
      const today = new Date().toISOString().split("T")[0];
      const payload = {
        question_id: questionId,
        answer_text: getAnswer,
      };

      try {
        let response = await fetch(
          `${BACKEND_URL}/api/rememberTree/daily-question/`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify(payload),
          }
        );

        if (response.status === 401) {
          //    
          const newAccessToken = await refreshAccessToken();
          if (newAccessToken) {
            response = await fetch(
              `${BACKEND_URL}/api/rememberTree/daily-question/`,
              {
                method: "POST",
                headers: {
                  "Content-Type": "application/json",
                  Authorization: `Bearer ${newAccessToken}`,
                },
                body: JSON.stringify(payload),
              }
            );
          } else {
            return;
          }
        }

        if (response.ok) {
          const jsonData = await response.json();
          console.log(" ");
          console.log(":", jsonData.questionText);
          sessionStorage.setItem("lastSubmissionDate", today);
          sessionStorage.setItem("submitAns", getAnswer);
          setSubmittedAnswer(getAnswer);
          setIsSubmitted(true);
          setAnswer("");
          handleShowToast("  5   .", 3000);
        } else {
          console.error("Failed to submit data");
          handleShowToast(
            "  .  .",
            3000
          );
        }
      } catch (error) {
        console.error("An error occurred", error);
        handleShowToast(
          "    .  .",
          3000
        );
      }
    } else if (isSubmitted) {
      handleShowToast("  .", 3000);
    }
  }

  function handleShowToast(message, duration) {
    if (toastTimer) clearTimeout(toastTimer);
    setToastMessage(message);
    const timer = setTimeout(() => setToastMessage(""), duration);
    setToastTimer(timer);
  }

  useEffect(() => {
    if (questRef.current) {
      const questElement = questRef.current;
      setIsExpanded(questElement.scrollHeight > questElement.clientHeight);
    }
  }, [isSubmitted]);

  useEffect(() => {
    function handleClickOutside(event) {
      if (
        answerWpRef.current &&
        !answerWpRef.current.contains(event.target) &&
        chatBoxRef.current &&
        !chatBoxRef.current.contains(event.target) &&
        questRef.current &&
        !questRef.current.contains(event.target)
      ) {
        if (isSubmitted) {
          if (closeTimer) clearTimeout(closeTimer);
          handleShowToast("5   .", 5000);
          const timer = setTimeout(() => {
            onClose();
          }, 5000);
          setCloseTimer(timer);
        } else {
          onClose();
        }
      } else {
        if (closeTimer) clearTimeout(closeTimer);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, [isSubmitted, onClose, closeTimer]);

  return (
    <>
      <div className={`${styles.container} ${styles.fadeIn}`}>
        <div className={styles.innerContainer}>
          <div className={styles.chatting}>
            <div
              className={`${styles.quest} ${
                isSubmitted ? styles.questShift : ""
              } ${isExpanded ? styles.expanded : ""}`}
              ref={questRef}
            >
              {myname} {question}
            </div>
            {isSubmitted && (
              <div className={styles.chatBox} ref={chatBoxRef}>
                {submittedAnswer}
              </div>
            )}
          </div>
          {!hideAnswerWp && (
            <div className={styles.answerWp} ref={answerWpRef}>
              <input
                name="answer"
                type="text"
                className={`${styles.answerBox} ${
                  isSubmitted ? styles.submitted : ""
                }`}
                placeholder={
                  isSubmitted
                    ? "    .     ."
                    : "   ."
                }
                value={getAnswer}
                onChange={(e) => setAnswer(e.target.value)}
                disabled={isSubmitted}
                onClick={() => {
                  if (isSubmitted) {
                    handleShowToast(
                      "  .   5   .",
                      3000
                    );
                  }
                }}
              />
              <div className={styles.submit}>
                <img
                  src="./static/img/answerSubmit.png"
                  alt="submit button"
                  onClick={submitAnswer}
                  style={{
                    cursor: isSubmitted ? "not-allowed" : "pointer",
                    filter: isSubmitted ? "grayscale(100%)" : "none",
                  }}
                />
              </div>
            </div>
          )}
        </div>
      </div>
      {toastMessage && <div className={styles.toast}>{toastMessage}</div>}{" "}
    </>
  );
}

export default TalkModal;
